.custom-checkout {
  & .required {
    color: #D62D30;
    text-decoration: none; }
  & h3 {
    font-weight: 700;
    font-size: get-vw(22px);
    line-height: 1.4;
    text-align: center;
    letter-spacing: 0.01em;
    color: #373F41;
    @include mobile() {
      font-size: get-vw-mobile(22px); } }
  & .woocommerce-billing-fields__field-wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 60%;
    margin: get-vw(20px) auto;
    @include mobile() {
      flex-direction: column;
      width: 100%;
      margin-top: get-vw-mobile(10px);
      margin-bottom: get-vw-mobile(20px); }
    & .form-row {
      width: 48%;
      display: flex;
      flex-wrap: wrap;
      margin-right: auto;
      @include mobile() {
        width: 100%;
        max-width: get-vw-mobile(320px);
        margin-top: get-vw-mobile(10px); }
      & .select2-container--default .select2-selection--single {
        border: none;
        border-radius: 0;
        height: unset;
        background: #F4F5F4;
        color: #737B7D;
        outline: none;
        @include mobile() {
          height: get-vw-mobile(42px); } }
      & .select2-container--default .select2-selection--single .select2-selection__rendered {
        font-size: get-vw(16px);
        line-height: 1.3;
        color: #373F41;
        padding: get-vw(10px) get-vw(15px);
        @include mobile() {
          font-size: get-vw-mobile(16px);
          padding: get-vw-mobile(10px) get-vw-mobile(15px); } }
      & .select2-container--default .select2-selection--single .select2-selection__arrow {
        height: 100%;
        right: 5%;
        & b {
          margin-top: unset;
          margin-left: unset;
          border-width: get-vw(5px) get-vw(4px) 0 get-vw(4px);
          // width: get-vw(5px)
 } }          // height: get-vw(5px)
      & .optional {
        display: none; }
      & label {
        width: 100%;
        font-size: get-vw(14px);
        line-height: get-vw(20px);
        letter-spacing: get-vw(0.25px);
        color: #737B7D;
        margin-top: get-vw(20px);
        margin-bottom: get-vw(5px);
        @include mobile() {
          font-size: get-vw-mobile(14px);
          line-height: 1;
          margin: get-vw-mobile(5px) 0; }
        &[for="billing_country"] {
          // margin-bottom: 0
          @include mobile() {
            margin-bottom: get-vw-mobile(5px); } } }
      & .woocommerce-input-wrapper {
        width: 100%;
        & strong {
          background: #f4f5f4;
          font-weight: 400;
          line-height: 1.3;
          color: #373f41;
          font-size: get-vw(16px);
          padding: get-vw(10px) get-vw(15px);
          width: 100%;
          display: block;
          @include mobile() {
            height: get-vw-mobile(42px);
            padding: 0 get-vw-mobile(15px);
            font-size: get-vw-mobile(14px);
            display: flex;
            align-items: center; } }
        & input {
          width: 100%;
          border: none;
          outline: none;
          font-size: get-vw(16px);
          line-height: 1.3;
          color: #373F41;
          padding: get-vw(10px) get-vw(15px);
          background: #F4F5F4;
          @include mobile() {
            height: get-vw-mobile(42px);
            padding: 0 get-vw-mobile(15px);
            font-size: get-vw-mobile(14px); } } } } } }

.shoppingCartWrapper .custom-checkout {
  & table {
    margin: get-vw(30px) auto;
    @include mobile() {
      margin: get-vw-mobile(15px) 0; }
    & tr {
      height: get-vw(60px);
      &.cart_item {
        @include mobile() {
          border-bottom: 1px solid rgba(219, 224, 225, 0.5);
          height: get-vw-mobile(50px); } }
      &.cart-subtotal {
        @include mobile() {
          height: get-vw-mobile(60px); } }
      th, td {
        @include mobile() {
          padding-bottom: get-vw-mobile(10px); } }
      & .product-name {
        width: get-vw(400px);
        @include mobile() {
          padding: 0;
          width: 60%; } }
      & .product-total {
        text-align: right; }
      &.cart-subtotal td,&.order-total td {
        text-align: right; }
      &.paczkomaty-shipping {
        & label {
          font-weight: 700;
          font-size: get-vw(16px);
          line-height: 1.2;
          letter-spacing: 0.01em;
          color: #373F41;
          margin-bottom: get-vw(10px);
          @include mobile() {
            font-size: get-vw-mobile(16px); } }
        & .select2-container--default .select2-selection--single {
          width: 100%;
          border: none;
          outline: none;
          font-size: get-vw(16px);
          line-height: 1.3;
          color: #373F41;
          padding: get-vw(10px) get-vw(15px);
          background: #F4F5F4;
          border-radius: 0;
          position: relative;
          height: get-vw(42px);
          @include mobile() {
            height: get-vw-mobile(40px);
            padding: get-vw-mobile(10px) get-vw-mobile(15px); } }
        & .select2-container .select2-selection--single .select2-selection__rendered {
          padding-left: unset;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          width: 97%;
          @include mobile() {
            font-size: get-vw-mobile(12px); } }
        & .select2.select2-container {
          width: 100%!important; }
        & .select2-container--default .select2-selection--single .select2-selection__arrow {
          height: 96%;
          @include mobile() {
            right: 4%; } }
        & td[colspan="2"] div:last-child {
          height: fit-content;
          margin-top: get-vw(20px);
          width: 100%;
          @include mobile() {
            margin-top: get-vw-mobile(20px); } }
        & #open-geowidget {
          display: block;
          width: fit-content;
          margin-left: auto;
          padding: get-vw(10px) get-vw(20px);
          background: #F9C722;
          color: #373F41;
          font-size: get-vw(16px);
          font-weight: 700;
          @include mobile() {
            font-size: get-vw-mobile(16px);
            padding: get-vw-mobile(10px) 0;
            width: 100%;
            text-align: center; }
          &:hover {
            @media(hover:hover) {
              color: $white;
              background: linear-gradient(130.16deg, #82BB58 2.89%, #53A580 97.29%); } } } } } }
  & .woocommerce-checkout-payment {
    display: flex;
    flex-wrap: wrap;
    width: 65%;
    margin: 0 auto get-vw(40px);
    @include mobile() {
      width: 100%; }
    & .wc_payment_methods {
      width: 40%;
      color: #373F41;
      font-size: get-vw(12px);
      line-height: get-vw(20px);
      letter-spacing: get-vw(0.25px);
      @include mobile() {
        width: 100%;
        font-size: get-vw-mobile(12px);
        line-height: 1.1;
        letter-spacing: get-vw-mobile(0.25px); } }
    & .form-row {
      width: 55%;
      margin-left: auto;
      white-space: nowrap;
      @include mobile() {
        width: 100%;
        margin-left: unset;
        margin-top: get-vw-mobile(10px); }
      & .woocommerce-privacy-policy-text, & .woocommerce-terms-and-conditions-checkbox-text {
        color: #373F41;
        font-size: get-vw(12px);
        line-height: get-vw(20px);
        letter-spacing: get-vw(0.25px);
        @include mobile() {
          font-size: get-vw-mobile(12px);
          line-height: 1.1;
          letter-spacing: get-vw-mobile(0.25px); }
        & a {
          color: #2686B8;
          text-decoration: underline;
          &:hover {
            text-decoration: none; } } }
      & button[type="submit"] {
        margin: get-vw(10px) 0 0 auto;
        background: #0D1018;
        border-radius: get-vw(40px);
        letter-spacing: get-vw(0.3px);
        color: #F4F5F4;
        border: none;
        outline: none;
        padding: get-vw(13px) get-vw(32px);
        font-size: get-vw(16px);
        display: block;
        cursor: pointer;
        transition: .4s ease-in-out;
        @include mobile() {
          padding: get-vw-mobile(13px) 0;
          text-align: center;
          width: 100%;
          margin: get-vw-mobile(20px) 0;
          font-size: get-vw-mobile(16px);
          border-radius: get-vw-mobile(40px); }
        &:hover {
          @media (hover: hover) {
            box-shadow: $btnShadow;
            color: $accentColor;
            text-shadow: 0 0 1px $accentColor, 0 0 1px $accentColor;
            background: $white; } } } } }
  & .woocommerce-shipping-methods {
    @include mobile() {
      text-align: right; } } }

.custom-checkout {
  @include mobile() {
    margin-top: get-vw-mobile(10px); }
  & .select2-selection__clear {
    @include mobile() {
      display: none; } } }
