.shoppingCartWrapper {
  @include mobile() {
    min-height: calc(100vh - 8.889vmin); }
  & * {
    box-sizing: border-box; }
  & table {
    border-collapse: collapse;
    & .product-subtotal {
      width: get-vw(200px);
      text-align: center;
      padding: 0 get-vw(20px);
      font-size: get-vw(17px);
      letter-spacing: get-vw(0.75px);
      color: #373F41;
      @include mobile() {
        width: initial;
        font-size: get-vw-mobile(14px);
        font-weight: 700; } }
    & .product-quantity {
      width: get-vw(160px);
      text-align: center;
      padding: 0 get-vw(20px);
      font-size: get-vw(17px);
      letter-spacing: get-vw(0.75px);
      color: #373F41;
      @include mobile() {
        display: none; }
      & .screen-reader-text {
        display: none; }
      & input {
        width: 80%;
        text-align: center;
        border-color: rgba(219, 224, 225, 0.5);
        outline: none;
        line-height: 2;
        &::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
          opacity: 1; } } }
    & .product-price {
      width: get-vw(200px);
      text-align: center;
      padding: 0 get-vw(20px);
      font-size: get-vw(17px);
      letter-spacing: get-vw(0.75px);
      color: #373F41;
      @include mobile() {
        display: none; } }
    & .product-name {
      width: get-vw(300px);
      text-align: start;
      @include mobile() {
        width: initial;
        padding: 0 get-vw-mobile(10px);
        text-align: center; }
      & a {
        font-weight: 800;
        font-size: get-vw(18px);
        line-height: 1.3;
        color: #373F41;
        pointer-events: none;
        @include mobile() {
          font-size: get-vw-mobile(13px); } } }
    & .product-remove {
      width: get-vw(90px);
      text-align: center;
      @include mobile() {
        display: none; }
      & a {
        font-weight: 900;
        font-size: get-vw(24px);
        @include mobile() {
          font-weight: 500;
          font-size: get-vw-mobile(20px); }
        @media (hover: hover) {
          &:hover {
            color: red; } } } }
    & .product-thumbnail {
      width: get-vw(160px);
      text-align: center;
      pointer-events: none;
      @include mobile() {
        width: initial;
        padding: get-vw-mobile(20px) 0; }
      & img {
        width: 75% !important;
        height: unset !important;
        box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.21);
        @include mobile() {
          width: auto !important;
          height: auto !important;
          max-width: get-vw-mobile(88px);
          max-height: get-vw-mobile(88px); } } }
    & tr {
      padding: get-vw(20px) 0;
      height: get-vw(200px);
      @include mobile() {
        padding: get-vw-mobile(20px) 0; }
      &.paczkomaty-shipping {
        @include mobile();
        margin-bottom: get-vw-mobile(20px); }
      &.fee {
        & td {
          text-align: right;
          & span {
            font-weight: 700;
            color: #D62D30; } } }
      &.coupon-wrap {
        height: get-vw(100px);
        @include mobile() {
          height: initial;
          padding: 0;
          margin-top: get-vw-mobile(15px);
          position: relative;
          display: block; }
        & .update-cart {
          @include mobile() {
            position: absolute;
            width: get-vw-mobile(320px);
            padding: 0;
            top: get-vw-mobile(80px); } }
        & .actions {
          @include mobile() {
            position: absolute;
            // padding: 0 get-vw-mobile(20px)
            padding-top: get-vw-mobile(20px);
            width: get-vw-mobile(320px); } }
        & .remove-cart {
          text-align: right; } }
      th {
        text-align: left; } } }
  & .cart {
    margin: get-vw(22px) 0;
    @include mobile() {
      width: 90%;
      margin: 0 auto get-vw-mobile(120px); }
    thead {
      @include mobile() {
        display: none; } }
    & .coupon {
      & label {
        display: block;
        margin-bottom: get-vw(10px);
        font-size: get-vw(15px);
        letter-spacing: get-vw(0.25px);
        color: #443E3E;
        @include mobile() {
          display: none; } }
      & .coupon-input {
        width: get-vw(255px);
        position: relative;
        @include mobile() {
          width: 100%; }
        & .input-text {
          border: 1px solid #EEEEEE;
          height: get-vw(42px);
          width: 100%;
          outline: none;
          padding: 0 get-vw(10px);
          @include mobile() {
            height: get-vw-mobile(42px);
            padding-left: get-vw-mobile(15px); } }
        & button {
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          height: get-vw(42px);
          width: get-vw(42px);
          background: linear-gradient(130.16deg, #82BB58 2.89%, #53A580 97.29%);
          outline: none;
          border: 0;
          cursor: pointer;
          transition: .4s ease-in-out;
          @include mobile() {
            height: get-vw-mobile(42px);
            width: get-vw-mobile(42px); }
          & svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            width: 20%;
            & path {
              transition: .4s ease-in-out; } }
          &:hover {
            @media (hover: hover) {
              background: #fff;
              text-shadow: 0 0 1px #82bb58, 0 0 1px #82bb58;
              box-shadow: 7px 4px 15px rgba(0, 0, 0, 0.15);
              & svg path {
                stroke: #82bb58; } } } } } }
    & thead {
      border: 1px solid rgba(219, 224, 225, 0.5);
      & tr {
        height: get-vw(52px); } }
    & tbody {
      & tr:not(:last-child) {
        border: 1px solid rgba(219, 224, 225, 0.5);
        @include mobile() {
          border-top: 0;
          border-right: 0;
          border-left: 0; } } } }
  & .shop_table.custom-cart__total {
    margin-left: auto;
    margin-top: 0;
    @include mobile() {
      width: 100%; }
    & tr.coupon-wrap {
      height: get-vw(120px); }
    & tr {
      height: unset;
      &.cart-subtotal,&.order-total,&.cart-discount {
        height: get-vw(60px);
        @include mobile() {
          height: initial;
          min-height: get-vw-mobile(40px); } }
      &.woocommerce-shipping-totals {
        height: get-vw(160px);
        @include mobile() {
          border-top: 1px solid rgba(219,224,225,.5); }
        & td {
          & .woocommerce-shipping-methods {
            margin-bottom: get-vw(20px);
            & li {
              // & label
              //   cursor: pointer
              & .woocommerce-Price-amount {
                margin-left: auto; }
              &:not(:last-child) {
                margin-bottom: get-vw(5px); } } } } }
      &.order-total {
        border-top: 1px solid rgba(219, 224, 225, 0.5); }
      &.cart-discount {
        & td {
          color: #D62D30;
          & .woocommerce-remove-coupon {
            font-size: 0;
            position: relative;
            &::before {
              content: 'x';
              color: #373F41;
              font-size: get-vw(20px);
              position: relative;
              top: -1px;
              font-weight: 700;
              transition: .4s ease-in-out;
              margin-left: get-vw(10px); }
            &:hover {
              @media(hover:hover) {
                &::before {
                  color: red; } } } } } } }
    tr {
      padding: 0;
      height: get-vw-mobile(40px); }
    & th {
      width: get-vw(180px);
      text-align: left;
      @include mobile() {
        padding-left: get-vw-mobile(20px); } }
    & td {
      width: get-vw(300px);
      text-align: right;
      @include mobile() {
        padding-right: get-vw-mobile(20px);
        padding-top: get-vw-mobile(20px);
        padding-bottom: get-vw-mobile(20px); } } }
  & .woocommerce-notices-wrapper {
    margin-left: auto;
    margin-bottom: get-vw(20px);
    width: fit-content;
    font-weight: 600;
    font-size: get-vw(24px);
    line-height: 1.4;
    letter-spacing: get-vw(0.25px);
    color: #373F41; }
  & .wc-proceed-to-checkout {
    width: fit-content;
    margin-left: auto;
    @include mobile() {
      margin-right: auto; } }
  & .update-cart {
    padding-top: get-vw(27px);
    @include mobile() {
      padding-top: get-vw-mobile(20px);
      padding-right: get-vw-mobile(5px); }
    & button {
      width: get-vw(235px);
      display: flex;
      align-items: center;
      justify-content: center;
      height: get-vw(44px);
      border: 1px solid #0D1018;
      border-radius: get-vw(40px);
      font-size: get-vw(16px);
      color: #0D1018;
      cursor: pointer;
      background: #fff;
      pointer-events: all;
      margin-left: auto;
      transition: .4s ease-in-out;
      letter-spacing: get-vw(0.3px);
      @include mobile() {
        width: 100%;
        height: get-vw-mobile(42px);
        border-radius: get-vw-mobile(40px);
        font-size: get-vw-mobile(14px);
        background: #0D1018;
        font-weight: 600;
        color: #fff; }
      &:hover {
        @media (hover: hover) {
          color: #82bb58;
          text-shadow: 0 0 1px #82bb58, 0 0 1px #82bb58;
          box-shadow: 7px 4px 15px rgba(0, 0, 0, 0.15);
          border-color: transparent; } }
      &[aria-disabled="true"] {
        border-color: rgba(13, 16, 24,.4);
        pointer-events: none;
        color: rgba(13, 16, 24,.4);
        @include mobile() {
          color: #fff;
          border-color: rgba(13, 16, 24,1);
          opacity: 0; } } } }
  & .woocommerce-shipping-methods {
    & input {
      opacity: 0;
      &:checked ~ label::after {
        transform: translate(get-vw(-17px), -50%) scale(.8);
        background-color: #0D1018; }
      &:checked ~ label::before {
        border-color: rgba(13, 16, 24,1); } }
    & label {
      position: relative;
      // &::after
