      //   content: ''
      //   transition: .4s ease-in-out
      //   width: get-vw(16px)
      //   height: get-vw(16px)
      //   border-radius: 50%
      //   border: 1px solid rgba(13, 16, 24,.4)
      //   position: absolute
      //   display: block
      //   top: 50%
      //   left: 0
      //   transform: translate(get-vw(-20px), -50%)
      // &:hover
      //   @media (hover: hover)
      //     &::after
      //       transform: translate(get-vw(-17px), -50%) scale(.6)
      //       background-color: rgba(13, 16, 24,.8)
      //     &::before
 } }      //       border: 1px solid rgba(13, 16, 24,.8)
  & .shipping-calculator-button {
    margin-top: get-vw(10px);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    position: relative;
    width: 100%;
    height: get-vw(44px);
    font-size: get-vw(16px);
    color: #0D1018;
    transition: .2s ease-in-out;
    &::after {
      content: '';
      position: relative;
      display: block;
      border-top: 1px solid #0D1018;
      border-left: 1px solid #0D1018;
      margin-left: get-vw(10px);
      width: get-vw(6px);
      height: get-vw(6px);
      transform: rotate(-135deg);
      top: get-vw(-2px);
      transition: .2s ease-in-out; }
    &:hover {
        @media (hover: hover) {
          color: #82bb58;
          &::after {
            border-color: #82bb58; } } } }
  & .woocommerce-shipping-calculator {
    text-align: left;
    & .shipping-calculator-form {
      margin-bottom: get-vw(20px); }
    & .select2-container--default .select2-selection--single {
      position: relative; }
    & .form-row {
      margin-bottom: get-vw(10px);
      font-size: get-vw(16px);
      color: #0D1018;
      & * {
        outline: none !important; }
      & .select2-container--default .select2-selection--single {
        border: 1px solid #EEEEEE;
        border-radius: 0; }
      & .select2-container .select2-selection--single .select2-selection__rendered {
        padding: 0 get-vw(10px); }

      & input {
        outline: none;
        font-size: get-vw(16px);
        color: #0D1018;
        padding: 0 get-vw(10px);
        line-height: 1.6;
        width: 100%;
        border: 1px solid #EEEEEE; } } }
  & p .button {
    width: get-vw(155px);
    display: flex;
    align-items: center;
    justify-content: center;
    height: get-vw(34px);
    border: 1px solid #0D1018;
    font-size: get-vw(16px);
    color: #0D1018;
    cursor: pointer;
    background: #fff;
    margin-left: auto;
    transition: .4s ease-in-out;
    letter-spacing: get-vw(0.3px);
    &:hover {
      @media (hover: hover) {
        color: #82bb58;
        text-shadow: 0 0 1px #82bb58, 0 0 1px #82bb58;
        box-shadow: 7px 4px 15px rgba(0, 0, 0, 0.15);
        border-color: transparent; } } }
  & .checkout-button {
    width: get-vw(235px);
    display: flex;
    align-items: center;
    justify-content: center;
    height: get-vw(44px);
    border: 1px solid #0D1018;
    border-radius: get-vw(40px);
    font-size: get-vw(16px);
    color: #fff;
    cursor: pointer;
    background: #0D1018;
    margin-left: auto;
    transition: .4s ease-in-out;
    letter-spacing: get-vw(0.3px);
    @include mobile() {
      width: 100%;
      min-width: get-vw-mobile(320px);
      height: get-vw-mobile(52px);
      border-radius: get-vw-mobile(40px);
      font-size: get-vw-mobile(16px); }
    & svg {
      position: relative;
      top: 1px;
      width: get-vw(17px);
      margin-left: get-vw(10px);
      & path {
        transition: .4s ease-in-out; } }
    &:hover {
      @media (hover: hover) {
        color: #82bb58;
        text-shadow: 0 0 1px #82bb58, 0 0 1px #82bb58;
        box-shadow: 7px 4px 15px rgba(0, 0, 0, 0.15);
        border-color: transparent;
        background: #fff;
        & svg path {
          fill: #82bb58; } } } } }

html {
  & .select2-container--open .select2-dropdown--below {
    border-color: #EEEEEE;
    border-radius: 0; }
  & .select2-container--default .select2-results__option--highlighted[aria-selected], .select2-container--default .select2-results__option--highlighted[data-selected] {
    background: linear-gradient(130.16deg, #82BB58 2.89%, #53A580 97.29%);
    outline: none; } }
