body {
  & .jquery-quickviewmodal.blocker {
    z-index: 300!important;
    @include mobile {
      z-index: 100!important; } }
  & .quickviewmodal {
    max-width: fit-content; }
  & .quickviewmodal a.close-quickviewmodal {
    z-index: 300;
    position: absolute;
    width: get-vw(24px);
    height: get-vw(24px);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    right: get-vw(24px);
    top: get-vw(24px);
    cursor: pointer;
    background: transparent;
    font-size: 0;
    @include mobile {
      width: get-vw-mobile(24px);
      height: get-vw-mobile(24px);
      background: transparent;
      right: 8%;
      top: 3%;
 }      // display: none
    &::after, &::before {
      content: '';
      display: inline-block;
      width: get-vw(18px);
      height: get-vw(2px);
      background: #373F41;
      position: absolute;
      transform: rotate(45deg);
      border-radius: get-vw(10px);
      @include mobile {
        background: $white;
        width: get-vw-mobile(16px);
        height: get-vw-mobile(3px); } }
    &::before {
      transform: rotate(-45deg); } }
  & .wapf-wrapper .dz-preview {
    position: absolute;
    left: 15px;
    color: #0D1018;
    width: 50%;
    top: 50%;
    transform: translateY(-75%); }
  & .js-sizes {
    background-color: #f4f5f4;
    margin-bottom: get-vw(20px);
    @include mobile() {
      margin-bottom: get-vw-mobile(20px); }
    & * {
      box-sizing: border-box; } }
  & .modal__body .variations {
    opacity: 0;
    position: absolute;
    pointer-events: none; } }

.size-info {
  margin: 0 auto 0 get-vw(12px);
  font-size: get-vw(14px);
  color: #737B7D;
  line-height: 1.6;
  transition: .4s ease-in-out;
  @include mobile() {
    font-size: get-vw-mobile(14px);
    margin: 0 auto 0 get-vw-mobile(12px); } }
.size-price {
  margin-right: get-vw(25px);
  font-style: normal;
  font-weight: 700;
  color: #737B7D;
  font-size: get-vw(16px);
  @include mobile() {
    font-size: get-vw-mobile(16px); } }

.custom-select.change-size {
  padding: 0;
  & .custom-select-trigger {
    padding-left: get-vw(15px);
    @include mobile() {
      padding-left: get-vw-mobile(15px); } } }
.popupWrapper {
  & .modal img {
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.07); }
  & .final-wrap {
