        @include mobile() {
          color: $lightTextTwo; } } } }
  & .woocommerce-variation {
    position: absolute;
    pointer-events: none;
    opacity: 0; }
  & .wapf-product-totals, & .quantity, & .product_meta, & button[type='submit'] {
