        width: 70%;
        height: 70%;
        border-radius: 50%;
        background-color: #373F41;
        opacity: 0;
        transition: .4s ease-in-out;
        @include mobile() {
          background: $lightTextTwo; } } }
    & .wapf-checkable {
      &:not(:last-child) .wapf-label-text {
        margin-right: get-vw(30px);
        @include mobile() {
          margin-right: get-vw-mobile(33px);
