.popupWrapper .wapf-field-container.js-custom-btn {
  @include mobile {
    margin-bottom: get-vw-mobile(10px); } }

.custom-select {
  transition: .4s ease-in-out;
  &.opened {
    box-shadow: 0px 3px 4px 0px rgba(0,0,0,.25);
    & .custom-options {
      display: block;
      opacity: 1;
      transform: translateY(0);
      visibility: unset;
      pointer-events: all; } } }

.custom-select-trigger {
  z-index: 21;
  background: #f4f5f4; }
.blocker {
  @include mobile {
    padding: 0;
    padding-top: get-vw-mobile(110px); } }
.stickyBody {
  .blocker {
    @include mobile {
      padding: 0;
      padding-top: get-vw-mobile(81px); } } }


.jquery-quickviewmodal {
  overflow: hidden;
  @include mobile() {
    overflow-x: hidden;
    overflow-y: scroll; } }
.error-popup {
  position: absolute;
  z-index: 200;
  background: #D62D30;
  border-radius: get-vw(3px);
  color: #fff;
  padding: get-vw(4px) get-vw(6px);
  left: get-vw(10px);
  font-size: get-vw(14px);
  display: none;
  @include mobile() {
    border-radius: get-vw-mobile(3px);
    padding: get-vw-mobile(4px) get-vw-mobile(6px);
    left: get-vw-mobile(10px);
    font-size: get-vw-mobile(14px); }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 15px;
    width: 6px;
    height: 6px;
    transform: translate(0,-50%) rotate(45deg);
    background: #d62d30; }
  .with-error & {
    display: block; } }

#modal-product-all.popupWrapper {
  & .modal__body_left {
    @include mobile() {
      display: none!important; } }
  & .modal__body {
    @include mobile() {
      flex-direction: unset; } } }
